body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
	background-color: #999999;
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	align-items: center;
}

.smallLogoComponent {
	justify-self: start;
	padding-left: 5%;
}

.htn:hover {
	cursor: pointer;
}

@media only screen 
	and (max-width: 768px) {
		.hackthenews {
			height: 40px;
		}

		.htn {
			height: 25px;
		}

		.navLinksComponent p {
			font-size: 70%;
		}
	}

.commenterBox {
	border: 1px solid black;
	background-color: rgba(222, 25, 144, 0.25);
	width: 25%;
	margin: 10px auto;	
	box-shadow: 4px 3px 5px 0 #ccc;
}

@media only screen 
	and (max-width: 768px) {
		.commenterBox{
			width: 85%;
		}

	}
.storyBox {
	border: 1px solid black;
	background-color: rgba(222, 25, 144, 0.25);
	width: 70%;
	margin: 10px auto;	
	box-shadow: 4px 3px 5px 0 #ccc;
}

@media only screen 
	and (max-width: 768px) {
		.storyBox{
			width: 85%;
		}

	}


.App {
  text-align: center;
}


